body * {
  outline: 1px solid lightgrey;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}


.example-block {
  border: 1px solid #000;

  font-size: 50px;
  margin: 0 0 30px 0;

  border-radius: 50%;
  overflow: hidden;

  opacity: 0;

  &:hover {
    border-radius: 0%;
    box-shadow: inset 5px 5px 5px 5px #000;

    transition: 2s;

    opacity: 100;
    
  }
}

.example-string {
  font-size: 50px;

  outline: 2px solid #000;
}

.example-image {
  overflow: hidden;
}